import React from "react";

import {
  Box,
  Button,
  Header,
  Main,
  Text,
  Card,
  CardHeader,
  CardBody,
  CheckBox,
} from "grommet";

import gogackicon from "../../assets/temp/gobackicon.png";
import homeicon from "../../assets/temp/homeicon.png";

import { Link } from "react-router-dom";
import iconBlueIda from "../../assets/icons/iconBlueIda.png";
import iconOrangeVuelta from "../../assets/icons/iconeOrangeVuelta.png";
import Stack from "@mui/material/Stack";

import radiusIconChecked from "../../assets/icons/radiusIconChecked.png";
import radiusIconUnchecked from "../../assets/icons/radiusIconUnchecked.png";
import webpayimg from "../../assets/webpayimage.png";
import paypalimg from "../../assets/paypalimage.png";

const Bus_mobile_pay_info = () => {
  const [pagamentoNumbero, setPagamentoNumero] = React.useState(0);
  return (
    <Main style={{ backgroundColor: "#FFF", height: "100%" }}>
      <Header pad={"medium"}>
        <Link to="/pasajes-bus">
          <img src={gogackicon} alt="" />
        </Link>
        <h2> Datos de pasajeros</h2>
        <Link to="/">
          <img src={homeicon} alt="" />
        </Link>
      </Header>

      <Box pad={"medium"} gap="medium">
        <Box gap="xxsmall">
          <Text size="large"> 2 - Detalles de la compra</Text>
          <Text
            size="medium"
            style={{ whiteSpace: "nowrap", color: "#66a6e9" }}
          >
            {" "}
            Santiago - Concepcin{" "}
          </Text>
          <Text
            size="medium"
            style={{ whiteSpace: "nowrap", color: "#66a6e9" }}
          >
            {" "}
            Lira bus - Mar, 20 Ab, 2022{" "}
          </Text>
          <Box gap="medium" style={{ marginTop: "3%" }}>
            <Box gap={"small"}>
              <div
                className="RetanguloIdaVuleta"
                style={{ backgroundColor: "rgb(45, 124, 255)" }}
              >
                <Text style={{ color: "white" }}>Ida</Text>
              </div>
              <Box direction="row" align="center" gap="xsmall">
                <img src={iconBlueIda} style={{ height: "30px" }} />
                <Box>
                  <Text style={{ whiteSpace: "nowrap" }}>Santiago, Chile</Text>
                  <Text style={{ whiteSpace: "nowrap" }}>
                    Terminal Sur, 10:30 AM
                  </Text>
                </Box>
              </Box>
            </Box>

            <Box gap={"xsmall"}>
              <div
                className="RetanguloIdaVuleta"
                style={{ backgroundColor: "#ff9800", width: "100px" }}
              >
                <Text style={{ color: "white" }}>Vuelta</Text>
              </div>

              <Box direction="row" align="center" gap="xsmall">
                <img src={iconOrangeVuelta} style={{ height: "30px" }} />
                <Box>
                  <Text style={{ whiteSpace: "nowrap" }}>
                    Concepcin, Chile
                  </Text>
                  <Text style={{ whiteSpace: "nowrap" }}>
                    Terminal Collao, 10:30 AM
                  </Text>
                </Box>
              </Box>
            </Box>
          </Box>
          <Text style={{ marginTop: "3%" }}> Duracin : 06:00 horas</Text>
          <hr className="LneaDetalles" />

          <Box gap={"small"}>
            <Text>
              {" "}
              3 x Saln Cama:{" "}
              <span
                style={{ float: "right", color: "#5193ff", fontWeight: "bold" }}
              >
                CLP $38.700
              </span>
            </Text>
            <Text
              style={{
                color: "#66a6e9",
                fontWeight: "bold",
                display: "flex",
                alignItems: "center",
                justifyContent: "ceter",
              }}
            >
              {" "}
              Ingrese cdigo descuento{" "}
            </Text>
            <Box direction="row">
              <input className="inputpagamentoStyle"></input>{" "}
              <span
                className="boxAplicar"
                style={{
                  float: "right",
                  color: "#66a6e9",
                  fontWeight: "bold",
                }}
              >
                Aplicar
              </span>
            </Box>
            <Text>
              {" "}
              Descuento:{" "}
              <span
                style={{ float: "right", color: "#df9a34", fontWeight: "bold" }}
              >
                CLP $3.870
              </span>
            </Text>
          </Box>
          {/* <Text> Cdigo promocional:  <span style={{ float: 'right', color: '#df9a34' }}>CLP $0</span></Text> */}
        </Box>

        <Box>
          <Text size="large"> 3 -Opciones del pago</Text>
        </Box>

        <Stack justifyContent="center" alignItems="center">
          <Stack
            direction="row"
            spacing={1}
            // className="wrapperPagamento"
          >
            {/* <label for="option-2" className={pagamentoNumbero === 0 ? "Rectngulo_ativo" : "Rectngulo_inaativo"} onClick={() => setPagamentoNumero(0)}>
                <img src={pagamentoNumbero === 0 ? radiusIconChecked : radiusIconUnchecked} style={{ height: '30%', marginRight: '3%' }} />
                <img src={compraquiimg} style={{ width: '120px' }} />
            </label> */}

            <label
              for="option-2"
              className={
                pagamentoNumbero === 1
                  ? "Rectngulo_ativo"
                  : "Rectngulo_inaativo"
              }
              onClick={() => setPagamentoNumero(1)}
            >
              <img
                src={
                  pagamentoNumbero === 1
                    ? radiusIconChecked
                    : radiusIconUnchecked
                }
                style={{ height: "30%", marginRight: "3%" }}
                alt=""
              />
              <img src={webpayimg} style={{ width: "120px" }} alt="" />
            </label>
            <label
              for="option-3"
              className={
                pagamentoNumbero === 2
                  ? "Rectngulo_ativo"
                  : "Rectngulo_inaativo"
              }
              onClick={() => setPagamentoNumero(2)}
            >
              <img
                alt=""
                src={
                  pagamentoNumbero === 2
                    ? radiusIconChecked
                    : radiusIconUnchecked
                }
                style={{ height: "30%", marginRight: "3%" }}
              />
              <img src={paypalimg} style={{ width: "120px" }} alt="" />
            </label>
          </Stack>
        </Stack>
        <Stack justifyContent="center" alignItems="center" spacing={5}>
          <Box direction="row" gap="small">
            <CheckBox
            // checked={true}
            // label="Termos de comprimisso teste teste teste teste teste teste teste teste  "
            // onChange={(event) => setChecked(event.target.checked)}
            />

            <Text size="small">
              {" "}
              Acepto los trminos y condiciones de la plataforma.{" "}
            </Text>
          </Box>

          <Button
            primary
            style={{ backgroundColor: "rgb(45, 124, 255)", padding: "5%" }}
          >
            <Text> Pagar </Text>
          </Button>
        </Stack>
      </Box>
    </Main>
  );
};

export default Bus_mobile_pay_info;
