import React from "react";
import { Main, Box, ResponsiveContext, Grid } from "grommet";
import HeaderCard from "../../components/admin/header_card";
import { CardBodyViajes } from "../../components/admin/body_card";
import { HeaderViajes } from "../../components/admin/header_geral";
import SidebarAdmin from "../../components/admin/sidebar_admin";
import moment from "moment";

export const Bus_desktop = () => {
  const size = React.useContext(ResponsiveContext);
  const [sidebar, setSidebar] = React.useState(false);

  const calcularDiffDatas = (x) => {
    let data = x;
    const dataSplit = data.split("/");
    const day = dataSplit[0];
    const month = dataSplit[1];
    const year = dataSplit[2];
    data = new Date(year, month - 1, day);

    const now = moment(new Date());
    const past = moment(data);
    const duration = moment.duration(now.diff(past));

    return duration.asDays();
  };

  React.useEffect(() => {
    let from = calcularDiffDatas("18/05/2022");
    let to = calcularDiffDatas("30/06/2022");

    if (from >= 0 && to <= 0) {
      console.log("Data no periodo");
    } else {
      console.log("fora do periodo");
    }
  }, []);
  return (
    <>
      <HeaderViajes />

      <Box
        background={{
          image: "url(https://i.ibb.co/vHSPD4Z/bck.jpg)",
          size: "100% 100%",
        }}
        style={{ width: "100vw", height: "100vh" }}
      >
        <Grid
          fill
          rows={["auto", "flex"]}
          columns={["auto", "flex"]}
          areas={[
            { name: "header", start: [0, 0], end: [1, 0] },
            { name: "sidebar", start: [0, 1], end: [0, 1] },
            { name: "main", start: [1, 1], end: [1, 1] },
          ]}
          height={
            size === "large" ? "92.7vh" : size === "medium" ? "89.4vh" : "88vh"
          }
        >
          {sidebar && <SidebarAdmin />}

          <Box gridArea="main" justify="center" align="center">
            <Main>
              <Box align="center" justify="center">
                <HeaderCard mt={"2%"} />

                <CardBodyViajes />
              </Box>
            </Main>
          </Box>
        </Grid>
      </Box>
    </>
  );
};

export default Bus_desktop;
