import { Box, Button, Header, Text } from "grommet";
import React from "react";
import { HeaderViajes } from "../components/admin/header_geral";
import iconBusticket from "../assets/favicon.ico";
import logo from "../assets/miniLogo.png";
import { useParams } from "react-router-dom";
import api from "../services/api";
import axios from "axios";
export const PaymentReturn = () => {
  const [info, setInfo] = React.useState([]);
  const [dateOrder, setDateOrder] = React.useState([]);
  const { code } = useParams();
  const [loading, setLoading] = React.useState(true);
  
  React.useEffect(() => {
    localStorage.removeItem("ruta_info");
    api.get("/get/order/status/" + code).then(({ data }) => {
      console.log(data);
      let dateOrder = new Date(data.info_pay.transaction_date);
      setDateOrder(dateOrder);
      setInfo(data);
      setLoading(false);
    });
  }, [code]);

  if (loading) return <></>;

  const handleClick = () => {
    const config = {
      headers: {
        "Access-Control-Allow-Origin": "*",
        "Access-Control-Allow-Methods": "GET,PUT,POST,DELETE,PATCH,OPTIONS",
      },
    };

    axios
      .post(
        "http://localhost:3333/",
        {
          code: code,
          from: info.service.from,
          to: info.service.to,
          terminal_from: info.service.terminal_from,
          terminal_to: info.service.terminal_to,
          leave_at_date: info.service.leave_at_date.replace(/-/g, "/"),
          leave_at_hour: info.service.leave_at_hour,
          customer: info.ticket[0].name,
          info_pays: {
            payment_type: info.info_pay.payment_type_code,
            authorization_code: info.info_pay.authorization_code,
            installments_amount: info.info_pay.installments_number,
            card_number: "XXXXXX" + info.info_pay.card_detail_card_number,
          },
          price: parseFloat(info.info_pay.amount).toLocaleString("es-CL", {
            style: "currency",
            currency: "CLP",
          }),
          tickets: info.ticket,
        },
        { responseType: "blob" },
        config
      )
      .then(({ data }) => {
        window.open(URL.createObjectURL(data));
        console.log(data);
      });
  };
  return (
    <Box gap="large">
      <Header
        background="#666666"
        pad={{
          right: "large",
          left: "large",
          top: "small",
          bottom: "small",
        }}
        style={{ width: "100%", zIndex: "2" }}
      >
        <Box direction="row">
          <img alt="" src={iconBusticket} style={{ height: "50px" }} />
          <img alt="" src={logo} style={{ height: "50px" }} />
        </Box>
      </Header>

      <Box justify="center" align="center">
        <div class="boarding-pass">
          <header>
            <img alt="" src={logo} style={{ height: "50px" }} />
            <div class="flight">
              <small>Nmero de orden:</small>
              <strong>{info.code}</strong>
            </div>
          </header>
          <section class="cities">
            <div class="city">
              <small>Origen</small>

              <strong>{info.info_trip.from}</strong>
            </div>
            <div class="city">
              <small>Destino</small>

              <strong>{info.info_trip.to}</strong>
            </div>
            <svg class="airplane">
              {/* <use xlink:href="#airplane"></use> */}
            </svg>
          </section>

          <section class="infos">
            <div class="places">
              <div class="box">
                <small>Dados del pago:</small>
                <strong>
                  <em>{info.code}</em>
                </strong>
              </div>
              <div class="box">
                <small>Cdigo de Autorizacin:</small>
                <strong>
                  <em>{info.info_pay.authorization_code}</em>
                </strong>
              </div>
              <div class="box">
                <small>Precio:</small>
                <strong>
                  <em>
                    {parseFloat(info.info_pay.amount).toLocaleString("es-CL", {
                      style: "currency",
                      currency: "CLP",
                    })}
                  </em>
                </strong>
              </div>
              <div class="box">
                <small>Forma de pago:</small>
                <strong>
                  <em>{info.info_pay.payment_type_code}</em>
                </strong>
              </div>
              <div class="box">
                <small>Cuotas:</small>
                <strong>{info.info_pay.installments_number}</strong>
              </div>
              <div class="box">
                <small>Precio de las cuotas</small>
                <strong>
                  {info.info_pay.installments_amount
                    ? parseFloat(
                        info.info_pay.installments_amount
                      ).toLocaleString("es-CL", {
                        style: "currency",
                        currency: "CLP",
                      })
                    : "Sin cuotas"}
                </strong>
              </div>
              <div class="box">
                <small>Fecha de compra:</small>
                <strong>{dateOrder.toLocaleDateString("pt-BR")}</strong>
              </div>
            </div>
            <div class="times">
              <div class="box">
                <small>Terminal origen</small>
                <strong>{info.service.terminal_from}</strong>
              </div>
              <div class="box">
                <small>Terminal Destino</small>
                <strong>{info.service.terminal_to}</strong>
              </div>
              <div class="box">
                <small>Salida:</small>
                <strong>{info.service.leave_at_hour}</strong>
              </div>
              <div class="box">
                <small>Salida fecha:</small>
                <strong>{info.service.leave_at_date.replace(/-/g, "/")}</strong>
              </div>
              <div class="box">
                <small>llegada:</small>
                <strong>{info.service.arrives_at_hour}</strong>
              </div>
              <div class="box">
                <small>llegada fecha:</small>
                <strong>
                  {info.service.arrives_at_date.replace(/-/g, "/")}
                </strong>
              </div>
            </div>
          </section>
          <section class="strap">
            <div class="box">
              {/* <div class="passenger">
            
                <strong>
                  Muchas gracias por seguir confiando en nuestra empresa. Lorem
                  Ipsum is simply dummy text of the printing and typesetting
                  industry. Lorem Ipsum has been the industry's standard dummy
                  text ever since the 1500s, when an unknown printer took a
                  galley of type and scrambled it to make a type specimen book.
                  It has survived not only five centuries, but also the leap
                  into electronic typesetting, remaining essentially unchanged.
                  It was popularised in the 1960s with the release{" "}
                </strong>
              </div> */}
            </div>
            {/* <svg class="qrcode"></svg> */}
          </section>
        </div>
      </Box>

      <Button onClick={handleClick}> Descargar boleto</Button>
    </Box>
  );
};

export default PaymentReturn;
