import React from "react";

import { Routes, Route, useLocation, Outlet } from "react-router-dom";
import { motion } from "framer-motion";
//screens
import Home from "./Home_mobile";
import Bus from "./Bus/Bus_mobile";
import BusList from "./Bus/Bus_mobile_list";
import BusSelect from "./Bus/Bus_mobile_select";
import Buspay from "./Bus/Bus_mobile_pay";
import BusPayInfo from "./Bus/Bus_mobile_pay_info";

import Transfer from "./Transfer/Transfer_mobile";
import TransferList from "./Transfer/Transfer_mobile_list";
import TransferSelect from "./Transfer/Transfer_mobile_select";
import Transferpay from "./Transfer/Transfer_mobile_pay";
import TransferPayInfo from "./Transfer/Transfer_mobile_pay_info";
import PaymentReturn from "../pages/Payment_return";

const PageLayout = ({ children }) => children;

const pageVariants = {
  initial: {
    opacity: 0,
  },
  in: {
    opacity: 1,
  },
  out: {
    opacity: 0,
  },
};

const pageTransition = {
  type: "tween",
  ease: "linear",
  duration: 0.5,
};

const AnimationLayout = () => {
  const { pathname } = useLocation();

  return (
    <PageLayout>
      <motion.div
        key={pathname}
        initial="initial"
        animate="in"
        variants={pageVariants}
        transition={pageTransition}
      >
        <Outlet />
      </motion.div>
    </PageLayout>
  );
}; 

function RoutesMobile() {
  return (
    <Routes>
      <Route element={<AnimationLayout />}>
        <Route path="/" element={<Home />} />
        <Route path="/pasajes-bus" element={<Bus />} />
        <Route path="/pasajes-bus/from/to/date" element={<BusList />} />
        <Route path="/pasajes-bus/ruta" element={<BusSelect />} />
        <Route path="/pasajes-bus/payment" element={<Buspay />} />
        <Route path="/pasajes-bus/payment/info" element={<BusPayInfo />} />

        <Route path="/pasajes-transfer" element={<Transfer />} />
        <Route path="/pasajes-transfer/from/to/date" element={<TransferList />} />
        <Route path="/pasajes-transfer/payment" element={<Transferpay />} />
        <Route path="/pasajes-transfer/payment/info" element={<TransferPayInfo />} />

        <Route path="/return-payment/:code/:token" element={<PaymentReturn />} />
      </Route>
    </Routes>
  );
}
 
export default RoutesMobile;
