import React from "react";

import HeaderMobile from "../../components-mobile/header";
import { Box, Main, Select, DateInput, Button, Text } from "grommet";
import iconSearch from "../../assets/icons/search-color.svg";
import iconIda from "../../assets/icons/ida-color.svg";
import iconVuelta from "../../assets/icons/vuelta-color.svg";
import { Link } from "react-router-dom";

const Bus_mobile = () => {
  const styles = {
    buttonTicket: {
      width: "20vh",
      height: "80%",
      backgroundColor: "#F5922F",
      color: "white",
      borderColor: "transparent",
      textAlign: "center",
      marginBottom: "2vh",
      marginTop: "3vh",
    },
    inputs: {
      backgroundColor: "rgb(234, 234, 234)",
      borderRadius: "18px",
      padding: "5%",
      width: "100%",
    },
    iconStyleLarge: {
      height: "25px",
      position: "absolute",
      marginLeft: "-3%",
      marginTop: "-0.7%",
    },
    iconStyleMedium: {
      height: "25px",
      position: "absolute",
      marginLeft: "-3%",
      marginTop: "-0.9%",
    },
  };

  return (
    <Main style={{ height: "100vh" }}>
      <HeaderMobile />
      <Box pad={"medium"}>
        <Box
          background={"#FFF"}
          pad="medium"
          style={{ borderRadius: "8px" }}
          gap="small"
        >
          <h2> Dnde ests? </h2>
          <Select
            style={styles.inputs}
            options={["Santiago"]}
            size={"medium"}
            placeholder={"Origen"}
            plain
            icon={
              <img
                alt=""
                src={iconSearch}
                style={{
                  height: "25px",
                  position: "absolute",
                  marginLeft: "-10%",
                  marginTop: "-3%",
                }}
              />
            }
          />

          <Select
            style={styles.inputs}
            options={["Concepcion"]}
            size={"medium"}
            placeholder={"Destino"}
            plain
            icon={
              <img
                alt=""
                src={iconSearch}
                style={{
                  height: "25px",
                  position: "absolute",
                  marginLeft: "-10%",
                  marginTop: "-3%",
                }}
              />
            }
          />
          <h2> Cundo viajas? </h2>

          <DateInput
            format="dd/mm/yyyy"
            placeholder="Ida"
            size="medium"
            style={styles.inputs}
            plain="false"
            icon={
              <img
                alt=""
                src={iconIda}
                style={{
                  height: "25px",
                  position: "absolute",
                  marginLeft: "-10%",
                  marginTop: "-3%",
                }}
              />
            }
          />
          <DateInput
            format="dd/mm/yyyy"
            placeholder="Vuelta"
            size="medium"
            style={styles.inputs}
            plain="false"
            icon={
              <img
                alt=""
                src={iconVuelta}
                style={{
                  height: "25px",
                  position: "absolute",
                  marginLeft: "-10%",
                  marginTop: "-3%",
                }}
              />
            }
          />

          <Link
            to="/from/to/date"
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              textDecoration: "none",
            }}
          >
            <Button
              primary
              style={styles.buttonTicket}
              // onClick={() => navigate("busticket/viajes/origendestino/Santiago/Concepcion/20-07-2022")}
            >
              <Text> Buscar</Text>
            </Button>
          </Link>
        </Box>
      </Box>
    </Main>
  );
};

export default Bus_mobile;
