import React from "react";

import { Box, Button, Header, Main, Text } from "grommet";

import gogackicon from "../../assets/temp/gobackicon.png";
import homeicon from "../../assets/temp/homeicon.png";

import { Link } from "react-router-dom";

import Stack from "@mui/material/Stack";
import { PaisesGerais, paises } from "../../data/country/paises";
import { validateRUT } from "validar-rut";
import { style } from "@mui/system";

const Transfer_mobile_pay = () => {
  const [tipoDoc, setTipoDoc] = React.useState("rut");

  const Documentos = () => {
    return (
      <Box
        gap="small"
        pad="medium"
        style={{ backgroundColor: "#EBEBEB", borderRadius: "18px" }}
      >
        <Stack alignItems="center">
          <Text style={{ color: "#0c95be", fontSize: "1rem" }}>
            Nacionalidad
          </Text>
          <Box width={"100%"}>
            <input
              className="selectpagamentoMenorStyle"
              list="pais"
              id="naciolaidade"
              name="nacionalidad"
              style={{ width: "95%" }}
            />

            <datalist id="pais">
              {paises.map((pais, key) => (
                <option value={pais.name} style={{ backgroundColor: "white" }}>
                  {pais.name}
                </option>
              ))}

              {PaisesGerais.map((pais, key) => (
                <option value={pais.name}>{pais.name}</option>
              ))}
            </datalist>
          </Box>
        </Stack>

        <Stack justifyContent={"center"} alignItems="center">
          <Text
            style={{
              whiteSpace: "nowrap",
              color: "#0c95be",
              fontSize: "1rem",
            }}
          >
            Tipo de Documento
          </Text>
          <Box width={"100%"}>
            <select
              className="selectpagamentoStyle"
              style={{ width: "95%" }}
              onChange={(e) => setTipoDoc(e.target.value)}
              value={tipoDoc}
            >
              <option value="rut">RUT</option>
              <option value="id">ID</option>
              <option value="pass">PASAPORTE</option>
            </select>
          </Box>
        </Stack>
        <Stack justifyContent={"center"} alignItems={"center"}>
          <Text style={{ whiteSpace: "nowrap", color: "#0c95be" }}>
            {" "}
            N Documento
          </Text>
          <Box width={"100%"}>
            {/* <TextInput
                            // placeholder="00.000.000-00"
                            className='inputpagamentoStyle'
                            size={'small'}

                        /> */}
            <input
              className="inputpagamentoStyle"
              style={{ width: "95%" }}
              onBlur={(e) => {
                if (validateRUT(e.target.value) === false) {
                  alert("Rut invalido");
                }
              }}
            />
          </Box>
        </Stack>
        {/* <Box direction="row" gap="large"> */}
        <Stack justifyContent={"center"} alignItems={"center"}>
          <Text style={{ color: "#0c95be" }}> Nombre</Text>
          <Box width={"100%"}>
            <input
              className="inputpagamentoStyle"
              style={{ width: "95%" }}
            ></input>
            {/* <TextInput
                            // placeholder="00.000.000-00"
                            className='inputpagamentoStyle'
                            size={'small'}

                        /> */}
          </Box>
        </Stack>

        <Stack justifyContent={"center"} alignItems={"center"}>
          <Text style={{ color: "#0c95be" }}> Apellido</Text>
          <Box width={"100%"}>
            {/* <TextInput
                            className='inputpagamentoStyle'
                            // placeholder="00.000.000-00"
                            size={'small'}/> */}
            <input
              className="inputpagamentoStyle"
              style={{ width: "95%" }}
            ></input>
          </Box>
        </Stack>
        {/* </Box> */}
      </Box>
    );
  };

  return (
    <Main style={{ height: "100vh" }}>
      <Header pad={"medium"}>
        <Link to="/pasajes-transfer">
          <img src={gogackicon} alt="" />
        </Link>
        <h2> Datos de pasajeros</h2>
        <Link to="/">
          <img src={homeicon} alt="" />
        </Link>
      </Header>

      <Box pad={"medium"} gap="medium">
        <Documentos />

        <Link
          to="/pasajes-transfer/payment/info"
          style={{
            textDecoration: "none",
            display: "flex",
            justifyContent: "center",
            marginTop: "1%",
          }}
        >
          <Button
            style={{
              backgroundColor: "#5193ff",
              padding: "2%",
              borderRadius: "18px",
            }}
          >
            <Text color={"white"}> Continuar </Text>
          </Button>
        </Link>
      </Box>
    </Main>
  );
};

export default Transfer_mobile_pay;
