import React from "react";
import { Box, CardBody, Header, Text } from "grommet";
import Stack from "@mui/material/Stack";
import { PaisesGerais, paises } from "../../data/country/paises";
import { validateRUT } from "validar-rut";

export const CardDePasajero = ({ seats, handleChange, handleContact }) => {
  const [dataInput, setDataInputs] = React.useState([]);
  const handleChangeInputs = (e) => {
    let date = {
      ...dataInput,
      [e.target.name]: e.target.value,
      seat: e.target.id,
    };

    console.log(date);
    // if (!date.seat) {
    //   date.seat = e.target.id;
    // }
    setDataInputs(date);
    handleChange(e.target.id, date);
  };
  return (
    <Box
      background="rgb(255, 255, 255)"
      style={{ borderRadius: "18px" }}
      pad="medium"
    >
      <Text size="large">1 - Informacin de los Pasajeros</Text>

      <CardBody pad="medium" gap="small" direction="column">
        {seats.map((seat) => (
          <Stack direction={"row"} spacing={2}>
            <Stack justifyContent={"center"} alignItems="center">
              <Text style={{ color: "#0c95be" }}> Asiento</Text>
              <div className="Rectngulo_3_copia_2">
                <Text color="white">{seat}</Text>
              </div>
            </Stack>

            <Stack justifyContent={"center"} alignItems="center">
              <Text style={{ color: "#0c95be" }}> Nacionalidad</Text>
              <Box width={"80%"}>
                <input
                  className="selectpagamentoMenorStyle"
                  list="pais"
                  id={seat}
                  name="nationality"
                  onChange={handleChangeInputs}
                />
                <datalist id="pais">
                  {paises.map((pais, key) => (
                    <option
                      value={pais.name}
                      style={{ backgroundColor: "white" }}
                    >
                      {pais.name}
                    </option>
                  ))}

                  {PaisesGerais.map((pais, key) => (
                    <option value={pais.name}>{pais.name}</option>
                  ))}
                </datalist>
              </Box>
            </Stack>

            <Stack justifyContent={"center"} alignItems="center">
              <Text style={{ whiteSpace: "nowrap", color: "#0c95be" }}>
                Tipo de Documento
              </Text>
              <Box width={"100%"}>
                <select
                  className="selectpagamentoStyle"
                  id={seat}
                  name="doc_type"
                  onChange={handleChangeInputs}
                  //   onChange={(e) => setTipoDoc(e.target.value)}
                  //   value={tipoDoc}
                >
                  <option value="">Seleccionar</option>
                  <option value="rut">RUT</option>
                  <option value="id">ID</option>
                  <option value="pass">PASAPORTE</option>
                </select>
              </Box>
            </Stack>

            <Stack justifyContent={"center"} alignItems={"center"}>
              <Text style={{ whiteSpace: "nowrap", color: "#0c95be" }}>
                N Documento
              </Text>
              <Box width={"100%"}>
                <input
                  className="inputpagamentoStyle"
                  id={seat}
                  name="doc"
                  onChange={handleChangeInputs}
                  onBlur={(e) => {
                    if (validateRUT(e.target.value) === false) {
                      alert("Rut invalido");
                    }
                  }}
                />
              </Box>
            </Stack>

            <Stack justifyContent={"center"} alignItems={"center"}>
              <Text style={{ color: "#0c95be" }}> Nombre</Text>
              <Box width={"100%"}>
                <input
                  id={seat}
                  className="inputpagamentoStyle"
                  name="name"
                  onChange={handleChangeInputs}
                />
              </Box>
            </Stack>

            <Stack justifyContent={"center"} alignItems={"center"}>
              <Text style={{ color: "#0c95be" }}> Apellido</Text>
              <Box width={"100%"}>
                <input
                  id={seat}
                  className="inputpagamentoStyle"
                  name="surname"
                  onChange={handleChangeInputs}
                />
              </Box>
            </Stack>
          </Stack>
        ))}

        <Header>
          <Box width={"30%"}>
            <Text style={{ whiteSpace: "nowrap", color: "#0c95be" }}>
              Email
            </Text>

            <input
              className="inputpagamentoStyle"
              name="email"
              onChange={handleContact}
              style={{ width: "100%" }}
            />
          </Box>

          <Box width={"30%"}>
            <Text style={{ whiteSpace: "nowrap", color: "#0c95be" }}>
              Email Confirmacin
            </Text>

            <input
              className="inputpagamentoStyle"
              name="email_confirm"
              onChange={handleContact}
            />
          </Box>

          <Box width={"30%"}>
            <Text style={{ whiteSpace: "nowrap", color: "#0c95be" }}>
              Telfono
            </Text>

            <input
              className="inputpagamentoStyle"
              name="phone"
              onChange={handleContact}
            />
          </Box>
        </Header>
      </CardBody>
    </Box>
  );
};

export default CardDePasajero;
