import React from "react";
import { Box } from "grommet";

import iconBusticket from "../assets/favicon.ico";
import logo from "../assets/mobile/miniLogo.png";

export const HeaderMobile = () => {
  return (
    <Box
      background="#666666"
      pad={{
        right: "large",
        left: "large",
        top: "small",
        bottom: "small",
      }}
      style={{ maxWidth: "100vw" }}
      direction="row"
      align="center"
      justify="center"
    >
      <img
        alt=""
        src={iconBusticket}
        style={{ height: "auto", width: "5vh" }}
      />

      <img alt="" src={logo} style={{ height: "auto", width: "60%" }} />
    </Box>
  );
};

export default HeaderMobile;
