import React from "react";

import { Box, Button, Header, Main, Text } from "grommet";

import gogackicon from "../../assets/temp/gobackicon.png";
import homeicon from "../../assets/temp/homeicon.png";

import blueicon from "../../assets/icons/iconBlueIda.png";
import orangeicon from "../../assets/icons/iconeOrangeVuelta.png";

import { Link } from "react-router-dom";

import {
  asentos1col,
  asentos1co2,
  asentos1co3,
  asentos1co4,
} from "../../data/onibus/onibusexp";

const Bus_mobile_select = () => {
  const styles = {
    buttonStylesOcupado: {
      borderRadius: "4px",
      backgroundColor: "rgb(217, 217, 217)",
      width: "30px",
      height: "30px",
      zIndex: "133",
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
    },

    buttonStylesLivre: {
      borderRadius: "4px",
      backgroundColor: "rgb(115, 223, 184)",
      width: "30px",
      height: "30px",
      zIndex: "133",
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
    },

    buttonStylesSelecionado: {
      borderRadius: "4px",
      backgroundColor: "rgb(29, 137, 251)",
      width: "30px",
      height: "30px",
      zIndex: "133",
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
    },

    buttonStylesDesativado: {
      borderRadius: "4px",
      backgroundColor: "rgb(29, 137, 251, 0)",
      width: "30px",
      height: "30px",
      zIndex: "133",
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
    },
  };

  return (
    <Main style={{ backgroundColor: "#FFF", height: "100vh" }}>
      <Header pad={"medium"}>
        <Link to="/pasajes-bus">
          <img src={gogackicon} alt="" />
        </Link>
        <h2> Selecciona tu asiento</h2>
        <Link to="/">
          <img src={homeicon} alt="" />
        </Link>
      </Header>

      <Box pad={"xsmall"} gap="medium">
        <Header
          border={[
            {
              color: "black",
              size: "xsmall",
              style: "solid",
              side: "bottom",
            },
          ]}
        >
          <Box>
            <Box direction="row" align="center" gap="small">
              <img src={blueicon} alt="" style={{ height: "30%" }} />
              <h2> Terminal Aeropueto Santiago</h2>
            </Box>
            <Box direction="row" align="center" gap="small">
              <img src={orangeicon} alt="" style={{ height: "30%" }} />
              <h2> Terminal Sul Concepcion</h2>
            </Box>
          </Box>
        </Header>

        <Text style={{ textAlign: "center" }}> Salon Cama</Text>

        <Box direction="row" justify="center" gap="xlarge" align="end">
          <Box gap="medium">
            <Box gap="medium" direction="row" align="center">
              <div
                className="circulosColorido"
                style={{
                  backgroundColor: "rgb(115, 223, 184)",
                  height: "25px",
                  width: "25px",
                }}
              >
                <Text style={{ color: "transparent" }}> bola verde </Text>
              </div>
              <Text
                className="textoCirulosViajem"
                style={{ fontSize: "0.5rem" }}
              >
                Libre
              </Text>
            </Box>

            <Box gap="30%" direction="row" align="center">
              <div
                className="circulosColorido"
                style={{
                  backgroundColor: "rgb(29, 137, 251)",
                  height: "25px",
                  width: "25px",
                }}
              >
                <Text style={{ color: "transparent" }}> bola azul </Text>
              </div>
              <Text
                className="textoCirulosViajem"
                style={{ fontSize: "0.5rem" }}
              >
                Seleccionado
              </Text>
            </Box>

            <Box gap="medium" direction="row" align="center">
              <div
                className="circulosColorido"
                style={{
                  backgroundColor: "rgb(217, 217, 217)",
                  height: "25px",
                  width: "25px",
                }}
              >
                <Text style={{ color: "transparent" }}> bola cinza </Text>
              </div>
              <Text
                className="textoCirulosViajem"
                style={{ fontSize: "0.5rem" }}
              >
                Ocupado
              </Text>
            </Box>
          </Box>

          <Box
            style={{
              border: "1px",
              borderColor: "black",
              borderStyle: "solid",
              borderRadius: "0.5rem",
              width: "60%",
              height: "100%",
              // height: "50vh",

              overflow: "scroll",
            }}
            pad="large"
            gap="small"
          >
            {asentos1col.map((assento, key) => (
              <Header>
                <Box direction="row" gap="small">
                  <Button
                    primary
                    color={"#D9D9D9"}
                    size={"small"}
                    style={styles.buttonStylesOcupado}
                  >
                    <Text
                      style={{
                        fontWeight: "bold",
                        fontSize: "1rem",
                      }}
                    >
                      {asentos1col[key].numero}
                    </Text>
                  </Button>

                  <Button
                    primary
                    color={"#D9D9D9"}
                    size={"small"}
                    style={styles.buttonStylesOcupado}
                  >
                    <Text
                      style={{
                        fontWeight: "bold",
                        fontSize: "1rem",
                      }}
                    >
                      {asentos1co2[key].numero}
                    </Text>
                  </Button>
                </Box>

                <Box direction="row" gap="small">
                  <Button
                    primary
                    color={"#D9D9D9"}
                    size={"small"}
                    style={styles.buttonStylesOcupado}
                  >
                    <Text
                      style={{
                        fontWeight: "bold",
                        fontSize: "1rem",
                      }}
                    >
                      {asentos1co3[key].numero}
                    </Text>
                  </Button>

                  <Button
                    primary
                    color={"#D9D9D9"}
                    size={"small"}
                    style={styles.buttonStylesOcupado}
                  >
                    <Text
                      style={{
                        fontWeight: "bold",
                        fontSize: "1rem",
                      }}
                    >
                      {asentos1co4[key].numero}
                    </Text>
                  </Button>
                </Box>
              </Header>
            ))}
          </Box>
        </Box>
        <Link
          to="/pasajes-bus/payment"
          style={{
            display: "flex",
            alignItems: "center",
            alignSelf: "center",
            justifyContent: "center",
            width: "90%",
            textDecoration: "none",
          }}
        >
          <Button
            style={{
              backgroundColor: "rgb(115, 223, 184)",
              padding: "5%",
              borderRadius: "18px",
            }}
          >
            <Text> Continuar </Text>
          </Button>
        </Link>
      </Box>
    </Main>
  );
};

export default Bus_mobile_select;
