import { useContext } from "react";
import { Button, ResponsiveContext } from "grommet";

export const Btn = ({ seat, HandleSelect, serivce_key }) => {
  const size = useContext(ResponsiveContext);
  const styles = {
    buttonStylesOcupado: {
      borderRadius: "4px",
      backgroundColor: "rgb(217, 217, 217)",
      width: size === "large" ? "43.5px" : "28.3px",
      height: size === "large" ? "44px" : "27.4px",
      zIndex: "133",
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
      fontWeight: "bold",
      fontSize: size === "large" ? "1.2rem" : "1rem",
    },

    buttonStylesLivre: {
      borderRadius: "4px",
      backgroundColor: "rgb(115, 223, 184)",
      width: size === "large" ? "43.5px" : "28.3px",
      height: size === "large" ? "44px" : "27.4px",
      zIndex: "133",
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
      fontWeight: "bold",
      fontSize: size === "large" ? "1.2rem" : "1rem",
    },

    buttonStylesSelecionado: {
      borderRadius: "4px",
      backgroundColor: "rgb(29, 137, 251)",
      width: size === "large" ? "43.5px" : "28.3px",
      height: size === "large" ? "44px" : "27.4px",
      zIndex: "133",
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
      fontWeight: "bold",
      fontSize: size === "large" ? "1.2rem" : "1rem",
    },

    buttonStylesDesativado: {
      borderRadius: "4px",
      backgroundColor: "rgb(29, 137, 251, 0)",
      width: size === "large" ? "43.5px" : "28.3px",
      height: size === "large" ? "44px" : "27.4px",
      zIndex: "133",
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
      fontWeight: "bold",
      fontSize: size === "large" ? "1.2rem" : "1rem",
    },
  };
  return (
    <Button
      primary
      color={"#D9D9D9"}
      size={"small"}
      style={
        seat.n_seat === 0
          ? styles.buttonStylesDesativado
          : seat.status.status === 0
          ? styles.buttonStylesLivre
          : seat.status.status === 5
          ? styles.buttonStylesSelecionado
          : styles.buttonStylesOcupado
      }
      id={seat.n_seat}
      name={seat.id}
      onClick={(e) => {
        if (seat.n_seat !== 0 && seat.status.status !== 1)
          HandleSelect(e, serivce_key, seat.id);
      }}
    >
      {seat.n_seat !== 0 ? seat.n_seat : ""}
    </Button>
  );
};

export default Btn;
