import React from "react";
import {
  Main,
  Box,
  Image,
  Button,
  Header,
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  Text,
  TextInput,
  DropButton,
  ResponsiveContext,
  Grid,
  Select,
  DateInput,
  DataTable,
  Meter,
  CheckBox,
  Tabs,
  Tab,
} from "grommet";
import background from "../../assets/background_home.png";

import Stack from "@mui/material/Stack";

import backmid from "../../assets/backgroundmiddle.jpg";

import iconBusticket from "../../assets/favicon.ico";
import logo from "../../assets/miniLogo.png";
import api from "../../services/api";

import Swal from "sweetalert2";

import iconSearch from "../../assets/icons/search.svg";
import iconSearch2 from "../../assets/icons/pin.svg";

import iconIda from "../../assets/icons/ida.svg";
import iconVuelta from "../../assets/icons/vuelta.svg";
import iconpeople_header from "../../assets/icons/iconpeople-blue-header.png";
import radiusIconChecked from "../../assets/icons/radiusIconChecked.png";
import radiusIconUnchecked from "../../assets/icons/radiusIconUnchecked.png";
import iconOrangeVuelta from "../../assets/icons/iconeOrangeVuelta.png";
import iconBlueIda from "../../assets/icons/iconBlueIda.png";

import webpayimg from "../../assets/webpayimage.png";
import paypalimg from "../../assets/paypalimage.png";
import { PaisesGerais, paises } from "../../data/country/paises";
import { validateRUT } from "validar-rut";
import Modal from "@mui/material/Modal";
import validator from "validator";

import CardDocs from "../../components/bus/Card_Docs";
import { Services } from "grommet-icons";
export const InfoClientes = () => {
  const [url_wp, setUrl] = React.useState("");
  const [token_wp, setToken] = React.useState("");

  const BoxHome = [
    {
      color: "transparent",
      size: "2vh",
      style: "solid",
      side: "left",
    },
    {
      color: "transparent",
      size: "2vh",
      style: "solid",
      side: "right",
    },
    {
      color: "transparent",
      size: "2vh",
      // style: "solid",
      side: "top",
    },
  ];

  const styleBackGroundHome = {
    backgroundColor: "#E0E0E0",
  };

  const [pagamentoNumbero, setPagamentoNumero] = React.useState(0);
  const size = React.useContext(ResponsiveContext);

  const styles = {
    center: {
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      backgroundImage: `url(${backmid})`,
    },
    centerLogo: {
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
    },
    prefooter: {
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
    },
    buttonTicket: {
      width: "45vh",
      height: "6vh",
      backgroundColor: "#F5922F",
      color: "white",
      borderColor: "transparent",
      textAlign: "center",
      marginBottom: "3vh",
    },
    buttonTicketPagar: {
      width: "20vh",
      height: "6vh",
      backgroundColor: "#F5922F",
      color: "white",
      borderColor: "transparent",
      textAlign: "center",
      marginBottom: "3vh",
    },
    buttonTicketPasajeros: {
      width: "24vh",
      height: "6vh",
      backgroundColor: "white",
      color: "gray",
      borderColor: "gray",
      textAlign: "center",
      marginBottom: "3vh",
      whiteSpace: "nowrap",
    },
    buttonTicketCancelar: {
      width: "12vh",
      height: "8vh",
      backgroundColor: "gray",
      color: "white",
      borderColor: "transparent",
      borderRadius: "7%",
      textAlign: "center",
    },
    buttonTicketQuantidade: {
      height: "4.5vh",
      width: "40%",
      backgroundColor: "#0c73df",
      color: "black",
      borderColor: "black",
      border: "2px",
      borderRadius: "7%",
      textAlign: "center",
    },
    dropMenuPosMedium: { marginLeft: "80vw", position: "absolute" },
    dropMenuPosLarge: { marginLeft: "85vw", position: "absolute" },
    dropMenuPosSmall: { marginRight: "5%" },
    buttonsNavbar: { whiteSpace: "nowrap" },

    datasButtonP: { backgroundColor: "#5CCFA6", width: "20%" },
    datasButton: { width: "20%" },
  };

  const [menuStatus, setMenuStatus] = React.useState(false);

  const ModalPagamento = () => {
    Swal.fire({
      title: "<strong>HTML <u>example</u></strong>",
      icon: "info",
      html:
        "You can use <b>bold text</b>, " +
        '<a href="//sweetalert2.github.io">links</a> ' +
        "and other HTML tags",
      showCloseButton: true,
      showCancelButton: true,
      focusConfirm: false,
      confirmButtonText: '<i class="fa fa-thumbs-up"></i> Great!',
      confirmButtonAriaLabel: "Thumbs up, great!",
      cancelButtonText: '<i class="fa fa-thumbs-down"></i>',
      cancelButtonAriaLabel: "Thumbs down",
    });
  };

  const [tipoDoc, setTipoDoc] = React.useState("rut");
  const [nDoc, setNDoc] = React.useState("");

  const validarDNI = (dni) => {
    var numero;
    var letr;
    var letra;
    var expresion_regular_dni;

    expresion_regular_dni = /^\d{8}[a-zA-Z]$/;

    if (expresion_regular_dni.test(dni) == true) {
      numero = dni.substr(0, dni.length - 1);
      letr = dni.substr(dni.length - 1, 1);
      numero = numero % 23;
      letra = "TRWAGMYFPDXBNJZSQVHLCKET";
      letra = letra.substring(numero, numero + 1);
      if (letra !== letr.toUpperCase()) {
        alert("Dni erroneo, la letra del NIF no se corresponde");
      } else {
        setNDoc(dni);
      }
    } else {
      alert("Dni erroneo, formato no vlido");
    }
  };

  const validar = (e) => {
    if (tipoDoc === "rut") {
      if (validateRUT(e.target.value) === false) {
        alert("Rut invalido");
      }
    } else if (tipoDoc === "id") {
      validarDNI(e.target.value);
    } else if (tipoDoc === "pass") {
      if (validator.isPassportNumber(e.target.value, "IN")) {
        setNDoc(e.target.value);
      } else {
        alert("Passaport erroneo, formato no vlido");
      }
    }
  };
  const Documentos = () => {
    return (
      <Stack direction={"row"} spacing={2}>
        <Stack justifyContent={"center"} alignItems="center">
          <Text style={{ color: "#0c95be" }}> Asiento</Text>
          {/* <Card background={'#4D91FF'} width="2vw">
                        <Stack justifyContent={'center'} alignItems="center"> */}
          <div className="Rectngulo_3_copia_2">
            <Text color="white">17</Text>
          </div>
          {/* </Stack>
                    </Card> */}
        </Stack>

        <Stack justifyContent={"center"} alignItems="center">
          <Text style={{ color: "#0c95be" }}> Nacionalidad</Text>
          <Box width={"80%"}>
            {/* <Select
                            className='selectpagamentoStyle'
                            height={'80%'}
                            options={['Chi', 'Bra', 'Arg']}
                            size={'small'}
                        /> */}

            {/* <select className="selectpagamentoMenorStyle" /> */}
            <input
              className="selectpagamentoMenorStyle"
              list="pais"
              id="naciolaidade"
              name="nacionalidad"
            />

            <datalist id="pais">
              {paises.map((pais, key) => (
                <option value={pais.name} style={{ backgroundColor: "white" }}>
                  {pais.name}
                </option>
              ))}

              {PaisesGerais.map((pais, key) => (
                <option value={pais.name}>{pais.name}</option>
              ))}
            </datalist>
          </Box>
        </Stack>

        <Stack justifyContent={"center"} alignItems="center">
          <Text style={{ whiteSpace: "nowrap", color: "#0c95be" }}>
            {" "}
            Tipo de Documento
          </Text>
          <Box width={"100%"}>
            {/* <Select
                            options={['RUT', 'PASS']}
                            size={'small'}
                        /> */}
            <select
              className="selectpagamentoStyle"
              onChange={(e) => setTipoDoc(e.target.value)}
              value={tipoDoc}
            >
              <option value="rut">RUT</option>
              <option value="id">ID</option>
              <option value="pass">PASAPORTE</option>
            </select>
          </Box>
        </Stack>

        <Stack justifyContent={"center"} alignItems={"center"}>
          <Text style={{ whiteSpace: "nowrap", color: "#0c95be" }}>
            {" "}
            N Documento
          </Text>
          <Box width={"100%"}>
            {/* <TextInput
                            // placeholder="00.000.000-00"
                            className='inputpagamentoStyle'
                            size={'small'}

                        /> */}
            <input
              className="inputpagamentoStyle"
              onBlur={(e) => {
                if (validateRUT(e.target.value) === false) {
                  alert("Rut invalido");
                }
              }}
            />
          </Box>
        </Stack>

        <Stack justifyContent={"center"} alignItems={"center"}>
          <Text style={{ color: "#0c95be" }}> Nombre</Text>
          <Box width={"100%"}>
            <input className="inputpagamentoStyle"></input>
            {/* <TextInput
                            // placeholder="00.000.000-00"
                            className='inputpagamentoStyle'
                            size={'small'}

                        /> */}
          </Box>
        </Stack>

        <Stack justifyContent={"center"} alignItems={"center"}>
          <Text style={{ color: "#0c95be" }}> Apellido</Text>
          <Box width={"100%"}>
            {/* <TextInput
                            className='inputpagamentoStyle'
                            // placeholder="00.000.000-00"
                            size={'small'}/> */}
            <input className="inputpagamentoStyle"></input>
          </Box>
        </Stack>
      </Stack>
    );
  };

  const Contato = () => {
    return (
      <Stack
        direction="row"
        justifyContent={"center"}
        alignItems="center"
        spacing={1}
      >
        <Box width="medium" align="center">
          <Text style={{ whiteSpace: "nowrap", color: "#0c95be" }}>Correo</Text>
          <input
            className="inputpagamentoStyle"
            type={"email"}
            style={{ width: "280px" }}
          ></input>
        </Box>

        <Box width="medium" align="center">
          <Text style={{ whiteSpace: "nowrap", color: "#0c95be" }}>
            Confirmacin de correo{" "}
          </Text>
          <input
            className="inputpagamentoStyle"
            type={"email"}
            style={{ width: "280px" }}
          ></input>
        </Box>
        <Box align="center">
          <Text style={{ whiteSpace: "nowrap", color: "#0c95be" }}>
            Telfono
          </Text>
          <input className="inputpagamentoStyle" type={"email"}></input>
        </Box>
      </Stack>
    );
  };

  const CardDePasajero = () => {
    return (
      <Box
        background="rgb(255, 255, 255)"
        style={{ borderRadius: "18px" }}
        pad="medium"
      >
        <Text size="large">1 - Informacin de los Pasajeros</Text>
        {/* // </CardHeader> */}

        <CardBody pad="medium" gap="small" direction="column">
          <Documentos />

          <Documentos />

          <Contato />
        </CardBody>
      </Box>
    );
  };

  const CardPagamento = () => {
    return (
      <Card height="100%" width="58vh" background="rgb(255, 255, 255)">
        <CardHeader pad="medium">
          <Box>
            <Text size="large"> 3 -Opciones del pago</Text>
          </Box>
        </CardHeader>

        <CardBody pad="medium" gap="small">
          <Stack justifyContent="center" alignItems="center">
            <Stack direction="row" spacing={1}>
              <label
                for="option-2"
                className={
                  pagamentoNumbero === 1
                    ? "Rectngulo_ativo"
                    : "Rectngulo_inaativo"
                }
                onClick={() => setPagamentoNumero(1)}
              >
                <img
                  alt=""
                  src={
                    pagamentoNumbero === 1
                      ? radiusIconChecked
                      : radiusIconUnchecked
                  }
                  style={{ height: "30%", marginRight: "3%" }}
                />
                <img src={webpayimg} style={{ width: "120px" }} alt="" />
              </label>
              <label
                for="option-3"
                className={
                  pagamentoNumbero === 2
                    ? "Rectngulo_ativo"
                    : "Rectngulo_inaativo"
                }
                onClick={() => setPagamentoNumero(2)}
              >
                <img
                  alt=""
                  src={
                    pagamentoNumbero === 2
                      ? radiusIconChecked
                      : radiusIconUnchecked
                  }
                  style={{ height: "30%", marginRight: "3%" }}
                />
                <img src={paypalimg} style={{ width: "120px" }} alt="" />
              </label>
            </Stack>
          </Stack>
          <Stack justifyContent="center" alignItems="center" spacing={5}>
            <Box direction="row" gap="small">
              <CheckBox />

              <Text size="small">
                Acepto los trminos y condiciones de la plataforma.{" "}
              </Text>
            </Box>

            <Button
              primary
              style={styles.buttonTicketPagar}
              onClick={handlePay}
            >
              <Text> Pagar </Text>
            </Button>
          </Stack>
        </CardBody>
      </Card>
    );
  };

  const handlePay = async () => {
    let response = await api.post("/create/ticket", {
      service,
      customer,
      contact,
    });

    localStorage.setItem("url", response.data.url);
    localStorage.setItem("token_webpay", response.data.token);

    setToken(response.data.token);
    setUrl(response.data.url);

    document.formulario.submit();
  };
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const [service, setService] = React.useState([]);
  const [loading, setLoading] = React.useState(true);
  const [customer, setCustomer] = React.useState([]);
  const [contact, setContact] = React.useState([]);
  const handleChange = (seat, value) => {
    // console.log(seat, name, value);
    setCustomer({ ...customer, [seat]: value });
  };
  const handleContact = (e) => {
    setContact({ ...contact, [e.target.name]: e.target.value });
  };
  React.useEffect(() => {
    setService(JSON.parse(localStorage.getItem("ruta_info")));
    setLoading(false);
  }, []);

  const HeaderGeral = () => {
    return (
      <Header
        background="#666666"
        pad={{
          right: "large",
          left: "large",
          top: "small",
          bottom: "small",
        }}
      >
        <Box direction="row">
          <img src={iconBusticket} style={{ height: "50px" }} />
          <img src={logo} style={{ height: "50px" }} />
        </Box>

        <Box direction="row" gap="medium" style={{ marginLeft: "-13%" }}>
          <Stack
            direction="row"
            justifyContent="center"
            alignItems="center"
            spacing={1}
          >
            {/* <Location /> */}
            <img src={iconSearch} style={{ height: "30px" }} />
            <Stack>
              <Text size="small"> Origen </Text>
              <Text> {service.from} </Text>
            </Stack>
          </Stack>
          <Stack
            direction="row"
            justifyContent="center"
            alignItems="center"
            spacing={1}
          >
            {/* Fecha */}
            {/* <Map /> */}
            <img src={iconSearch2} style={{ height: "30px" }} />
            <Stack>
              <Text size="small"> Destino </Text>
              <Text> {service.to}</Text>
            </Stack>
          </Stack>
          <Stack
            direction="row"
            justifyContent="center"
            alignItems="center"
            spacing={1}
          >
            {/* <TurnRightIcon /> */}
            <img src={iconIda} style={{ height: "30px" }} />
            <Stack>
              <Text size="small"> Fecha </Text>
              <Text>{transformDate(service.leave_at)}</Text>
            </Stack>
          </Stack>
          {/* <Stack
            direction="row"
            justifyContent="center"
            alignItems="center"
            spacing={1}
          >
            <img src={iconVuelta} style={{ height: "30px" }} />
            <Stack>
              <Text size="small"> Fecha regreso </Text>
              <Text> Sb, 20 Mar</Text>
            </Stack>
          </Stack> */}
        </Box>

        <Box direction="row" gap="large">
          <Button
            label="Mi cuenta"
            color={"transparent"}
            onClick={() => setMenuStatus(!menuStatus)}
          />

          {/* <User /> */}
          <img
            src={iconpeople_header}
            style={{ height: "50px", color: "white" }}
          />
        </Box>
      </Header>
    );
  };

  const transformDate = (date) => {
    let date_from = date.split("-");

    var date_convert = new Date(date_from[2], date_from[1], date_from[0]);

    return date_convert.toLocaleDateString("es-cl", {
      month: "short",
      weekday: "short",
      day: "numeric",
    });
  };

  return (
    <>
      {loading ? (
        <> </>
      ) : (
        <>
          <HeaderGeral />

          <div className="progressBar">
            <div className="circle">
              {" "}
              <span className="label">1</span>
              <span className="title">Ida</span>
            </div>{" "}
            <span className="bar done"></span>
            <div className="circle">
              {" "}
              <span className="label">2</span>
              <span className="title">Vuelta</span>
            </div>{" "}
            <span className="bar active"></span>
            <div className="circle active">
              {" "}
              <span className="label">3</span>
              <span className="title">Pago</span>
            </div>{" "}
            <span className="bar"></span>
            <div className="circle">
              {" "}
              <span className="label">4</span>
              <span className="title">Confirmacin</span>
            </div>
            {/* <span className="bar"></span> */}
            {/* circle done */}
          </div>

          <Main>
            <Box style={styleBackGroundHome}>
              <Box border={BoxHome} pad="medium">
                <Stack justifyContent="center" alignItems="center">
                  <Box
                    direction={size === "large" ? "row" : "column"}
                    gap="xsmall"
                  >
                    <Box gap="small">
                      {/* <CardDePasajero /> */}

                      <CardDocs
                        seats={service.n_seats}
                        handleChange={handleChange}
                        handleContact={handleContact}
                      />
                    </Box>

                    <Box
                      gap="small"
                      align={size === "large" ? "start" : "center"}
                      background="#fff"
                      pad={"medium"}
                      style={{ borderRadius: "18px" }}
                    >
                      {/* <Card
                        height={size === 'large"' ? "42.5vh" : "100%"}
                        align={size === "large" ? "start" : "center"}
                        width={size === 'large"' ? "58vh" : "100%"}
                        background="rgb(255, 255, 255)"
                      >
                        <CardHeader pad="medium"> */}
                      <Box direction="row" gap="100%">
                        <Box gap="xxsmall">
                          <Text size="large"> 2 - Detalles de la compra</Text>
                          <Text
                            size="medium"
                            style={{
                              whiteSpace: "nowrap",
                              color: "#66a6e9",
                            }}
                          >
                            {service.from} - {service.to}
                          </Text>
                          <Text
                            size="medium"
                            style={{
                              whiteSpace: "nowrap",
                              color: "#66a6e9",
                            }}
                          >
                            {" "}
                            {service.company} -{" "}
                            {transformDate(service.leave_at)}
                          </Text>
                          <Box
                            direction="row"
                            gap="medium"
                            style={{ marginTop: "3%" }}
                          >
                            <Box gap={"small"}>
                              <div
                                className="RetanguloIdaVuleta"
                                style={{
                                  backgroundColor: "rgb(45, 124, 255)",
                                }}
                              >
                                <Text style={{ color: "white" }}>Ida</Text>
                              </div>
                              <Box direction="row" align="center" gap="xsmall">
                                <img
                                  src={iconBlueIda}
                                  style={{ height: "30px" }}
                                />
                                <Box>
                                  <Text style={{ whiteSpace: "nowrap" }}>
                                    {service.from}
                                  </Text>
                                  <Text style={{ whiteSpace: "nowrap" }}>
                                    {service.info.terminal_from},{" "}
                                    {service.info.leave_at_hour}
                                  </Text>
                                </Box>
                              </Box>
                            </Box>

                            <Box gap={"xsmall"}>
                              <div
                                className="RetanguloIdaVuleta"
                                style={{
                                  backgroundColor: "#ff9800",
                                  width: "100px",
                                }}
                              >
                                <Text style={{ color: "white" }}>Vuelta</Text>
                              </div>

                              <Box direction="row" align="center" gap="xsmall">
                                <img
                                  src={iconOrangeVuelta}
                                  style={{ height: "30px" }}
                                />
                                <Box>
                                  <Text style={{ whiteSpace: "nowrap" }}>
                                    {service.to}
                                  </Text>
                                  <Text>
                                    {service.info.terminal_to},{" "}
                                    {service.info.leave_at_hour}
                                  </Text>
                                </Box>
                              </Box>
                            </Box>
                          </Box>
                          <Text style={{ marginTop: "3%" }}>
                            {" "}
                            Duracin : {service.info.duraction} hrs
                          </Text>
                          <hr className="LneaDetalles" />

                          <Box gap={"small"}>
                            <Text>
                              {" "}
                              3 x Saln Cama:{" "}
                              <span
                                style={{
                                  float: "right",
                                  color: "#5193ff",
                                  fontWeight: "bold",
                                }}
                              >
                                CLP{" "}
                                {service.price.toLocaleString("es-CL", {
                                  style: "currency",
                                  currency: "CLP",
                                })}
                              </span>
                            </Text>
                            <Text
                              style={{
                                color: "#66a6e9",
                                fontWeight: "bold",
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "ceter",
                              }}
                            >
                              {" "}
                              Ingrese cdigo descuento{" "}
                              <input className="inputpagamentoStyle"></input>{" "}
                              <span
                                className="boxAplicar"
                                style={{
                                  float: "right",
                                  color: "#66a6e9",
                                  fontWeight: "bold",
                                }}
                              >
                                Aplicar
                              </span>
                            </Text>
                            <Text>
                              {" "}
                              Descuento:{" "}
                              <span
                                style={{
                                  float: "right",
                                  color: "#df9a34",
                                  fontWeight: "bold",
                                }}
                              >
                                CLP $0
                              </span>
                            </Text>
                          </Box>
                          {/* <Text> Cdigo promocional:  <span style={{ float: 'right', color: '#df9a34' }}>CLP $0</span></Text> */}
                        </Box>
                      </Box>
                      {/* </CardHeader>
                      </Card> */}
                      <Box
                        className="bgTotal"
                        width="58vh"
                        height="7vh"
                        style={{ cursor: "pointer" }}
                        onClick={handleOpen}
                        // onClick={() => {
                        //   api
                        //     .post("/create/ticket", {
                        //       service,
                        //       customer,
                        //       contact,
                        //     })
                        //     .then(({ data }) => {
                        //       console.log(data);
                        //     });
                        // }}
                      >
                        <Text style={{ color: "white", fontWeight: "bold" }}>
                          {" "}
                          Total{" "}
                          <span
                            style={{
                              float: "right",
                              color: "white",
                              fontWeight: "bold",
                            }}
                          >
                            CLP{" "}
                            {service.price.toLocaleString("es-CL", {
                              style: "currency",
                              currency: "CLP",
                            })}
                          </span>
                        </Text>
                      </Box>
                    </Box>
                  </Box>
                </Stack>
              </Box>
            </Box>
          </Main>
        </>
      )}

      <form method="post" action={url_wp} name="formulario">
        <input type="hidden" name="token_ws" value={token_wp} />
      </form>

      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box align="center" margin={"10%"}>
          <CardPagamento />
        </Box>
      </Modal>
    </>
  );
};

export default InfoClientes;
