import React from 'react';
import Stack from '@mui/material/Stack';

import back_menu from '../../assets/icons/backgroun-menu.png';
import heli_menu from '../../assets/icons/heli-menu.png';
import barco_menu from '../../assets/icons/barco-menu.png';
import bus_menu from '../../assets/icons/bus-menu.png';
import transfer_menu from '../../assets/icons/transfer-menu.png';
import viajeespecial_menu from '../../assets/icons/viajeespecial-menu.png';

import { Link } from "react-router-dom";


export const HeaderCard = ({ mt }) => {

    const styles = {
        div: {
            backgroundImage: `url(${back_menu})`,
            backgroundSize: 'contain',
            backgroundRepeat: 'no-repeat',
            width: '1000px',
            height: '100px',
            position: 'absolute',
            marginTop: mt,
            display: 'flex',
            justifyContent: 'center',
            alignItem: 'center'
        }
    }


    return (
        <div style={styles.div}>
            <Stack direction='row' justifyContent="center"
                alignItems="center"
                spacing={4}>
                <Link to="/busticket/viajes">
                    <img alt="" src={bus_menu} style={{ height: '80px' }} />
                </Link>
                <Link to="/busticket/transfer">
                    <img alt="" src={transfer_menu} style={{ height: '80px' }} />
                </Link>
                <Link to="/busticket/viajes-especiales">
                    <img alt="" src={viajeespecial_menu} style={{ height: '80px' }} />
                </Link>
                <Link to="#">
                    <img alt="" src={barco_menu} style={{ height: '80px' }} />
                </Link>
                <Link to="#">
                    <img alt="" src={heli_menu} style={{ height: '80px' }} />
                </Link>
            </Stack>
        </div>
    )
};

export default HeaderCard;




