import React, { useEffect, useState } from "react";
import {
  Box,
  Button,
  Header,
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  Text,
  ResponsiveContext,
  Select,
  DateInput,
  Footer,
} from "grommet";
import Stack from "@mui/material/Stack";
import backmid from "../../assets/backgroundmiddle.jpg";
import lira from "../../assets/lirabus.png";
import api from "../../services/api";
import { Location, Map } from "grommet-icons";
import linepng from "../../assets/line.png";
import busback from "../../assets/icons/busBack.png";
import OnibusExecutivo from "../viajes/buses/BusExecutivo";
import OnibusSemiMaisCama1Andar from "../viajes/buses/SemPlusCama1";
import OnibusSemiMaisCama2Andar from "../viajes/buses/SemPlusCama2";
import Onibus155 from "../viajes/buses/Bus1551";
import Onibus1552Andar from "../viajes/buses/Bus1552";
import { Link, useParams, useNavigate } from "react-router-dom";
import assento1 from "../../assets/assento.png";

import BtnBus from "./bus_btn";

export const List_buses = () => {
  const navigate = useNavigate();
  const handleContinue = (service) => {
    localStorage.removeItem("ruta_info");
    const data = {
      n_seats: selecteds,
      qty_seats: selecteds.length,
      price: seatsValue,
      from: from,
      to: to,
      terminal_from: service.terminal_from,
      terminal_to: service.terminal_to,
      leave_at: leave_at,
      service: service.id,
      info: service,
      company: service.company.name,
      leave_at_hour: service.leave_at_hour,
    };

    localStorage.setItem("ruta_info", JSON.stringify(data));

    navigate("/pasajes-bus/payment");
  };
  const { from, to, leave_at, return_at } = useParams();
  const [services, setServices] = useState([]);
  const [bck, setBck] = useState([]);
  const [firstFloor, setFirstFloor] = useState(true);
  const [selecteds, setSelcted] = useState([]);
  const [selectedsStr, setSelctedStr] = useState([]);
  const [rld, setRld] = useState(true);
  const [seatsValue, setSeatsValue] = useState(0);

  React.useEffect(() => {
    api.get(`/services/${leave_at}/${from}/${to}`).then(({ data }) => {
      localStorage.setItem("services", JSON.stringify(data));
      setBck(data);
      setServices(data);
    });
  }, []);

  const [currentOpen, setCurrentOpen] = React.useState([]);

  const clearCache = async (key) => {
    const bc = localStorage.getItem("services");
    await setLoading(true);
    setServices(JSON.parse(bc));
    setSelcted([]);
    setSeatsValue(0);
    setCurrentOpen(key);
    setLoading(false);
  };
  const size = React.useContext(ResponsiveContext);

  const styles = {
    center: {
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      backgroundImage: `url(${backmid})`,
    },
    centerLogo: {
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
    },
    prefooter: {
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
    },
    buttonTicket: {
      width: "22vh",
      height: "6vh",
      backgroundColor: "#F5922F",
      color: "white",
      borderColor: "transparent",
      textAlign: "center",
      marginBottom: "3vh",
    },
    buttonComprarOpenModal: {
      width: "15vh",
      height: "5vh",
      backgroundColor: "#F5922F",
      color: "white",
      borderColor: "transparent",
      textAlign: "center",
    },
    buttonTicketPasajeros: {
      width: "24vh",
      height: "6vh",
      backgroundColor: "white",
      color: "gray",
      borderColor: "gray",
      textAlign: "center",
      marginBottom: "3vh",
      whiteSpace: "nowrap",
    },
    buttonTicketCancelar: {
      width: "12vh",
      height: "8vh",
      backgroundColor: "gray",
      color: "white",
      borderColor: "transparent",
      borderRadius: "7%",
      textAlign: "center",
    },
    buttonTicketQuantidade: {
      height: "4.5vh",
      width: "40%",
      backgroundColor: "#0c73df",
      color: "black",
      borderColor: "black",
      border: "2px",
      borderRadius: "7%",
      textAlign: "center",
    },
    dropMenuPosMedium: { marginLeft: "80vw", position: "absolute" },
    dropMenuPosLarge: { marginLeft: "85vw", position: "absolute" },
    dropMenuPosSmall: { marginRight: "5%" },
    buttonsNavbar: { whiteSpace: "nowrap" },

    datasButtonP: { backgroundColor: "#5CCFA6", width: "20%" },
    datasButton: { width: "20%" },
    buttonStylesOcupado: {
      borderRadius: "4px",
      backgroundColor: "rgb(217, 217, 217)",
      width: size === "large" ? "43.5px" : "28.3px",
      height: size === "large" ? "44px" : "27.4px",
      zIndex: "133",
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
    },

    buttonStylesLivre: {
      borderRadius: "4px",
      backgroundColor: "rgb(115, 223, 184)",
      width: size === "large" ? "43.5px" : "28.3px",
      height: size === "large" ? "44px" : "27.4px",
      zIndex: "133",
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
      fontWeight: "bold",
      fontSize: size === "large" ? "1.2rem" : "1rem",
    },

    buttonStylesSelecionado: {
      borderRadius: "4px",
      backgroundColor: "rgb(29, 137, 251)",
      width: size === "large" ? "43.5px" : "28.3px",
      height: size === "large" ? "44px" : "27.4px",
      zIndex: "133",
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
      fontWeight: "bold",
      fontSize: size === "large" ? "1.2rem" : "1rem",
    },

    buttonStylesDesativado: {
      borderRadius: "4px",
      backgroundColor: "rgb(29, 137, 251, 0)",
      width: size === "large" ? "43.5px" : "28.3px",
      height: size === "large" ? "44px" : "27.4px",
      zIndex: "133",
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
      fontWeight: "bold",
      fontSize: size === "large" ? "1.2rem" : "1rem",
    },
  };

  const transformDate = (date) => {
    let date_from = date.split("-");

    var date_convert = new Date(date_from[2], date_from[1], date_from[0]);

    return date_convert.toLocaleDateString("es-cl", {
      month: "short",
      weekday: "short",
      day: "numeric",
    });
  };

  const [from_date, setFromDate] = useState("");

  useEffect(() => {
    console.clear();
    let data = {
      date: leave_at,
      from: from,
      to: to,
    };

    // let date = leave_at.replace('-', '/');
    let date_from = leave_at.split("-");

    var date_convert = new Date(date_from[2], date_from[1], date_from[0]);

    setFromDate(
      date_convert.toLocaleDateString("es-cl", {
        month: "short",
        weekday: "short",
        day: "numeric",
      })
    );
  }, []);

  const display = {
    none: { display: "none" },
    inline: { display: "inline" },
    block: { block: "block" },
  };
  const [loading, setLoading] = React.useState(true);

  const HandleSelect = async (e, key, seat_id, n_seat, unselect) => {
    // console.log(key);
    // return 1;
    setLoading(true);
    let data = services;

    let index = services[key].seats.findIndex((seat) => seat.id === seat_id);

    if (data[key].seats[index].status.status === 5) {
      data[key].seats[index].status.status = 0;
      let value = data[key].seats[index].price;
      value = seatsValue - value;
      setSeatsValue(value);
      setSelcted(selecteds.filter((select) => select !== e.target.id));
    } else {
      data[key].seats[index].status.status = 5;
      let value = data[key].seats[index].price;
      value = value + seatsValue;
      setSeatsValue(value);
      setSelcted([...selecteds, e.target.id]);
    }

    await setServices(data);

    setLoading(false);
  };

  const BoxBus = {
    className: "busBackgroundProp",
    style: {
      backgroundImage: `url(${busback})`,
      backgroundRepeat: "no-repeat",
      zIndex: "1",
    },
    pad: { top: "large", bottom: "large", left: "large" },
    align: "center",
    justify: "center",
  };

  const BoxBtn = {
    align: "center",
    justify: "center",
    gap: "small",
    direction: "row",
  };

  const ReturnBtn = (seats, serivce_key, line, floor) => {
    return (
      <Box {...BoxBtn}>
        {seats
          .filter((seat) => seat.line === line && seat.floor === floor)
          .map((seat) => (
            <BtnBus
              HandleSelect={HandleSelect}
              seat={seat}
              serivce_key={serivce_key}
            />
          ))}
      </Box>
    );
  };

  const Premium = ({ seats, serivce_key }) => {
    return (
      <Box {...BoxBus}>
        {loading === false ? (
          <>
            <Box gap="large" style={firstFloor ? display.inline : display.none}>
              <Box gap="small">{ReturnBtn(seats, serivce_key, 3, "1")}</Box>

              <Box gap="small">
                {ReturnBtn(seats, serivce_key, 2, "1")}

                {ReturnBtn(seats, serivce_key, 1, "1")}
              </Box>
            </Box>
            <Box gap="large" style={firstFloor ? display.none : display.inline}>
              <Box gap="small">{ReturnBtn(seats, serivce_key, 3, "2")}</Box>

              <Box gap="small">
                {ReturnBtn(seats, serivce_key, 2, "2")}

                {ReturnBtn(seats, serivce_key, 1, "2")}
              </Box>
            </Box>
          </>
        ) : (
          "loading"
        )}
      </Box>
    );
  };

  const SemiCamaPlusCama = ({ seats, serivce_key }) => {
    return (
      <Box {...BoxBus}>
        {loading === false ? (
          <>
            <Box gap="large" style={firstFloor ? display.inline : display.none}>
              <Box gap="small">{ReturnBtn(seats, serivce_key, 3, "1")}</Box>

              <Box gap="small">
                {ReturnBtn(seats, serivce_key, 2, "1")}
                {ReturnBtn(seats, serivce_key, 1, "1")}
              </Box>
            </Box>
            <Box gap="large" style={firstFloor ? display.none : display.inline}>
              <Box gap="small">
                {ReturnBtn(seats, serivce_key, 4, "2")}
                {ReturnBtn(seats, serivce_key, 3, "2")}
              </Box>

              <Box gap="small">
                {ReturnBtn(seats, serivce_key, 2, "2")}
                {ReturnBtn(seats, serivce_key, 1, "2")}
              </Box>
            </Box>
          </>
        ) : (
          "loading"
        )}
      </Box>
    );
  };

  const Executivo = ({ seats, serivce_key }) => {
    return (
      <Box {...BoxBus}>
        {loading === false ? (
          <>
            <Box gap="large">
              <Box gap="small">
                {ReturnBtn(seats, serivce_key, 4, "1")}
                {ReturnBtn(seats, serivce_key, 3, "1")}
              </Box>

              <Box gap="small">
                {ReturnBtn(seats, serivce_key, 2, "1")}
                {ReturnBtn(seats, serivce_key, 1, "1")}
              </Box>
            </Box>
          </>
        ) : (
          "loading"
        )}
      </Box>
    );
  };

  return (
    <>
      <Box gap="small">
        <div
          style={{
            backgroundColor: "rgb(75, 199, 214)",
            position: "static",
            width: "100%",
            height: "10vh",
            borderRadius: "50px",
          }}
        >
          <CardBody pad="medium" direction="row">
            <Header
              pad={{
                right: "10%",
                left: size === "large" ? "12%" : "0%",
                top: size === "large" ? "small" : "none",
                bottom: "small",
              }}
              gap={"25%"}
            >
              <Text
                style={{
                  whiteSpace: "nowrap",
                  marginLeft: "20%",
                  color: "white",
                }}
              >
                Empresa A\Z
              </Text>
              <Text
                style={{
                  whiteSpace: "nowrap",
                  marginLeft: "8%",
                  color: "white",
                }}
              >
                Hora de salida
              </Text>
              <Text
                style={{
                  whiteSpace: "nowrap",
                  marginLeft: "2%",
                  color: "white",
                }}
              >
                Mejor Precio
              </Text>
              <Text
                style={{
                  whiteSpace: "nowrap",
                  marginLeft: "5%",
                  color: "white",
                }}
              >
                Mejor Ranking
              </Text>
            </Header>
          </CardBody>
        </div>

        <Box style={{ width: "80vw" }} gap="small">
          {services.map((info, key) => (
            <>
              <Box
                direction="row"
                pad="medium"
                gap={size === "large" ? "10%" : "7%"}
                align="center"
                background="rgb(255, 255, 255)"
                style={{ width: "100%", borderRadius: "18px" }}
              >
                <Header width="100%">
                  <img
                    src={lira}
                    style={{ width: "120px", height: "30px" }}
                    alt=""
                  />
                  <Stack
                    direction="row"
                    justifyContent="center"
                    alignItems="center"
                  >
                    <Stack justifyContent="center" alignItems="center">
                      <Text
                        style={{
                          whiteSpace: "nowrap",
                          color: "#4e98e6",
                          fontWeight: "bold",
                        }}
                      >
                        {info.leave_at_hour} am
                      </Text>
                      <Text
                        style={{
                          whiteSpace: "nowrap",
                          color: "#4e98e6",
                          fontSize: "15px",
                        }}
                      >
                        {transformDate(info.leave_at_date)}
                      </Text>
                    </Stack>
                    <Stack justifyContent="center" alignItems="center">
                      <Text
                        style={{
                          whiteSpace: "nowrap",
                          color: "#4e98e6",
                          fontSize: "15px",
                        }}
                      >
                        {info.duraction} hrs
                      </Text>
                      <img src={linepng} style={{ height: "30px" }} />
                    </Stack>
                    <Stack justifyContent="center" alignItems="center">
                      <Text
                        style={{
                          whiteSpace: "nowrap",
                          color: "#4e98e6",
                          fontWeight: "bold",
                        }}
                      >
                        {info.arrives_at_hour} am
                      </Text>
                      <Text
                        style={{
                          whiteSpace: "nowrap",
                          color: "#4e98e6",
                          fontSize: "15px",
                        }}
                      >
                        {transformDate(info.arrives_at_date)}
                      </Text>
                    </Stack>
                  </Stack>

                  <Stack justifyContent="center" alignItems="center">
                    <Text
                      style={{
                        whiteSpace: "nowrap",
                        color: "#4e98e6",
                        fontWeight: "bold",
                      }}
                    >
                      {info.seat_type_f1}
                    </Text>
                    <Text
                      style={{
                        whiteSpace: "nowrap",
                        color: "#4e98e6",
                        fontSize: "15px",
                      }}
                    >
                      {info.seat_type_f2 && info.seat_type_f2}
                    </Text>
                  </Stack>
                  <Box>
                    <Text
                      style={{
                        whiteSpace: "nowrap",
                        color: "#4e98e6",
                        fontWeight: "bold",
                      }}
                    >
                      {info.price_f1.toLocaleString("es-CL", {
                        style: "currency",
                        currency: "CLP",
                      })}
                    </Text>

                    {info.price_f2 && (
                      <Text
                        style={{
                          whiteSpace: "nowrap",
                          color: "#4e98e6",
                          fontWeight: "bold",
                        }}
                      >
                        {info.price_f2.toLocaleString("es-CL", {
                          style: "currency",
                          currency: "CLP",
                        })}
                      </Text>
                    )}
                  </Box>
                  <Button
                    primary
                    style={styles.buttonComprarOpenModal}
                    onClick={() => {
                      clearCache(key);
                    }}
                  >
                    <Text style={{ whiteSpace: "nowrap" }}> Comprar </Text>
                  </Button>
                </Header>
              </Box>
              <CardFooter
                // id={key}
                direction={"column"}
                // style={{ display: "none" }}
                style={currentOpen === key ? display.inline : display.none}
              >
                <Card
                  background="rgb(255, 255, 255)"
                  style={{ marginTop: "1%" }}
                >
                  <CardHeader>
                    <Header
                      style={{ width: "100%" }}
                      pad={{
                        left: "large",
                        right: "large",
                        top: "small",
                        bottom: "small",
                      }}
                    >
                      <Box>
                        <Text style={{ fontWeight: "bold" }}>
                          Terminal de salida
                        </Text>
                        <Text size="small">
                          {info.terminal_from} {info.leave_at_hour} hrs
                        </Text>
                      </Box>

                      <Box>
                        <Text style={{ fontWeight: "bold" }}>
                          Terminal de llegada
                        </Text>
                        <Text size="small">
                          {info.terminal_to} {info.arrives_at_hour} hrs
                        </Text>
                      </Box>
                      <Box width={"medium"}>
                        <div className="switch-button">
                          <input
                            className="switch-button-checkbox"
                            type="checkbox"
                            id={key + "checkboxAndar"}
                            onClick={() => {
                              setFirstFloor(!firstFloor);
                              setSelcted(selecteds);
                            }}
                          />
                          {info.vehicle_id !== 4 && (
                            <label className="switch-button-label" for="">
                              <span className="switch-button-label-span">
                                Piso 1
                              </span>
                            </label>
                          )}
                        </div>
                      </Box>

                      <Box>
                        <Button
                          onClick={() => {
                            clearCache(null);
                          }}
                          label="Cancelar"
                          color="#4e98e6"
                        />
                      </Box>
                    </Header>
                  </CardHeader>
                  <CardBody direction="row" justify="center" gap="medium">
                    <Box width={"65%"}>
                      {info.vehicle_id === 3 ? (
                        <SemiCamaPlusCama
                          seats={info.seats}
                          serivce_key={key}
                        />
                      ) : info.vehicle_id === 4 ? (
                        <Executivo seats={info.seats} serivce_key={key} />
                      ) : (
                        <Premium seats={info.seats} serivce_key={key} />
                      )}
                    </Box>

                    <Box>
                      <Text size={"xlarge"} style={{ whiteSpace: "nowrap" }}>
                        Tu asiento
                      </Text>
                      <Box gap="xsmall" direction="row" align="center">
                        <Text size={"small"} style={{ whiteSpace: "nowrap" }}>
                          Asiento
                        </Text>
                        <div
                          className="circulosColorido"
                          style={{
                            backgroundColor: "rgb(29, 137, 251)",
                            width: "12px",
                            height: "12px",
                          }}
                        >
                          {" "}
                          <Text style={{ color: "transparent" }}>
                            {" "}
                            bola azul{" "}
                          </Text>
                        </div>
                      </Box>
                      <Box>
                        <Text>
                          {selecteds
                            .sort((a, b) => a - b)
                            .map((s, key) => (
                              <Text
                                size={"large"}
                                style={{
                                  // whiteSpace: "wrap",
                                  marginTop: "20px",
                                  fontSize: "36px",
                                }}
                              >
                                {s}, {"\n"}
                              </Text>
                            ))}
                        </Text>
                      </Box>
                      {/* <Text
                        id="spanAssentos"
                        size={"large"}
                        style={{
                          whiteSpace: "nowrap",
                          marginTop: "20px",
                          fontSize: "36px",
                        }}
                      >
                        {selectedsStr}
                      </Text> */}
                      {/* <Text size={'small'} style={{ whiteSpace: 'nowrap' }}> $3.500</Text> */}
                      <Text
                        size={"small"}
                        style={{ whiteSpace: "nowrap", marginTop: "60px" }}
                      >
                        {" "}
                        Ms de un pasajero?
                      </Text>
                      <Text size={"small"} style={{ whiteSpace: "nowrap" }}>
                        {" "}
                        Seleccione ms de uno asiento
                      </Text>
                    </Box>
                    <img
                      alt=""
                      src={assento1}
                      style={{
                        height: "300px",
                      }}
                    />
                  </CardBody>

                  <CardFooter>
                    <Footer width={"100%"} pad="small">
                      <Stack direction="row" spacing={8}>
                        <Box gap="medium" direction="row" align="center">
                          <div
                            className="circulosColorido"
                            style={{ backgroundColor: "rgb(115, 223, 184)" }}
                          >
                            <Text style={{ color: "transparent" }}>
                              {" "}
                              bola verde{" "}
                            </Text>
                          </div>
                          <Text
                            className="textoCirulosViajem"
                            style={{ fontSize: "10px" }}
                          >
                            {" "}
                            Libre
                          </Text>
                        </Box>

                        <Box gap="30%" direction="row" align="center">
                          <div
                            className="circulosColorido"
                            style={{ backgroundColor: "rgb(29, 137, 251)" }}
                          >
                            <Text style={{ color: "transparent" }}>
                              bola azul{" "}
                            </Text>
                          </div>
                          <Text
                            className="textoCirulosViajem"
                            style={{ fontSize: "10px" }}
                          >
                            Seleccionado
                          </Text>
                        </Box>

                        <Box gap="medium" direction="row" align="center">
                          <div
                            className="circulosColorido"
                            style={{ backgroundColor: "rgb(217, 217, 217)" }}
                          >
                            <Text style={{ color: "transparent" }}>
                              {" "}
                              bola cinza{" "}
                            </Text>
                          </div>
                          <Text
                            className="textoCirulosViajem"
                            style={{ fontSize: "10px" }}
                          >
                            {" "}
                            Ocupado
                          </Text>
                        </Box>
                      </Stack>
                      <Box align="center">
                        <Text
                          size={"medium"}
                          style={{ whiteSpace: "nowrap", fontWeight: "bold" }}
                        >
                          {" "}
                          Nmero de asientos:{" "}
                          <Text size="large"> {selecteds.length}</Text>
                        </Text>
                        {/* <Link to="/pasajes-bus/payment"> */}
                        <Button
                          primary
                          style={styles.buttonTicket}
                          onClick={() => {
                            handleContinue(info);
                          }}
                        >
                          <Text style={{ whiteSpace: "nowrap" }}>
                            Continuar{" "}
                            {seatsValue.toLocaleString("es-CL", {
                              style: "currency",
                              currency: "CLP",
                            })}
                          </Text>
                        </Button>
                        {/* </Link> */}
                      </Box>
                    </Footer>
                  </CardFooter>
                </Card>
              </CardFooter>
            </>
          ))}
        </Box>
      </Box>
    </>
  );
};

export default List_buses;
