import React from 'react';
import { Grommet } from 'grommet';

//routes
import RoutesDesktop from './pages-desktop/App_desktop';
import RoutesMobile from './pages-mobile/App_mobile';
import { isBrowser } from 'react-device-detect';

function App() {
  const theme = {
    global: {
      font: {
        family: 'Poppins',
        size: '10px',
        height: '20px',
      },
      colors: {
        focus: '#0C85FF'
      },
      button: {
        default: {
          color: 'text',
          border: undefined,
          padding: {
            horizontal: '12px',
            vertical: '8px',
          },

        },
        primary: {
          background: { color: 'blue' },
          border: undefined,
          color: 'text-strong',
          font: { weight: 'bold' },
          padding: {
            horizontal: '12px',
            vertical: '8px',

          },

        },
        secondary: {
          border: { color: 'brand', width: '4px' },
          color: 'text',
          padding: {
            horizontal: '8px',
            vertical: '4px',

          },

        },
        active: {
          background: { color: 'brand-contrast' },
          color: 'text',
          secondary: {
            background: 'none',
            border: {
              color: 'brand-contrast',

            },

          },

        },
        disabled: {
          opacity: 0.3,
          secondary: {
            border: { color: 'text-weak' },

          },

        },
        hover: {
          background: { color: 'active' },
          secondary: {
            border: { color: 'active' },

          },

        },
      },

    },
    select: {

    },
    button: {

      padding: {
        horizontal: '0px',
      }

    },
    checkBox: {
      color: 'green',
      hover: {
        border: {
          color: 'none'
        }
      },
      extend: `
      color: #9C9C9C;
    `,
    },
  };

  return (
    <Grommet theme={theme} >
      {isBrowser ? <RoutesDesktop /> : <RoutesMobile />}
      
    </Grommet >
  );

}

export default App;
