import React from 'react';
import { Box, Button, Text, ResponsiveContext } from 'grommet';
import Stack from '@mui/material/Stack';

import { User, Home, Calendar, Note, Transaction, Logout, } from 'grommet-icons';
import DisplaySettingsIcon from '@mui/icons-material/DisplaySettings';
import AccountBalanceWalletIcon from '@mui/icons-material/AccountBalanceWallet';
import ReceiptLongIcon from '@mui/icons-material/ReceiptLong';

export const SidebarAdmin = () => {
    const size = React.useContext(ResponsiveContext);

    const styles = {
        buttonsNavbar: { whiteSpace: 'nowrap' },
    }
    return (
        <Box
            gridArea="sidebar"
            background="dark-3"
            width="17vw"
            animation={[
                { type: 'fadeIn', duration: 300 },
                { type: 'slideRight', size: '2xl', duration: 150 },
            ]}
            pad={{ left: 'small', top: 'large' }}
            style={{ zIndex: '0', height: '100vh' }}
        >
            <Stack>
                <Text style={{ textAlign: 'center' }}> MENU DE NVEGACIN</Text>
            </Stack>

            <Button href="#" hoverIndicator>
                <Box pad={{ horizontal: 'medium', vertical: 'small' }}>
                    <Text style={styles.buttonsNavbar}>  <Home /> Dashboard </Text>
                </Box>
            </Button>

            <Button href="#" hoverIndicator>
                <Box pad={{ horizontal: 'medium', vertical: 'small' }}>
                    <Text style={styles.buttonsNavbar}> <DisplaySettingsIcon /> Parmetros </Text>
                </Box>
            </Button>

            <Button href="#" hoverIndicator>
                <Box pad={{ horizontal: 'medium', vertical: 'small' }}>
                    <Text style={styles.buttonsNavbar}> <Calendar /> Reportes de ventas</Text>
                </Box>
            </Button>

            <Button href="#" hoverIndicator>
                <Box pad={{ horizontal: 'medium', vertical: 'small' }}>
                    <Text style={styles.buttonsNavbar}> <AccountBalanceWalletIcon /> Reportes de gastos </Text>
                </Box>
            </Button>

            <Button href="#" hoverIndicator>
                <Box pad={{ horizontal: 'medium', vertical: 'small' }}>
                    <Text style={styles.buttonsNavbar}> <Note /> Reportes de cierra </Text>
                </Box>
            </Button>

            <Button href="#" hoverIndicator>
                <Box pad={{ horizontal: 'medium', vertical: 'small' }}>
                    <Text > <ReceiptLongIcon /> Reportes de tickets eliminados </Text>
                </Box>
            </Button>

            <Button href="#" hoverIndicator>
                <Box pad={{ horizontal: 'medium', vertical: 'small' }}>
                    <Text style={styles.buttonsNavbar}> <User /> Cuenta</Text>
                </Box>
            </Button>
            <Button href="#" hoverIndicator>
                <Box pad={{ horizontal: 'medium', vertical: 'small' }}>
                    <Text style={styles.buttonsNavbar}> <Transaction /> Venta web </Text>
                </Box>
            </Button>

            <Button href="#" hoverIndicator>
                <Box pad={{ horizontal: 'medium', vertical: 'small' }}>
                    <Text style={styles.buttonsNavbar}><Logout /> Cerrar session </Text>
                </Box>
            </Button>
        </Box>
    )
};

export default SidebarAdmin;
