import React from "react";
import { Routes, Route } from "react-router-dom";

//screens
import Home from "./bus/Bus_desktop";
import BusList from "./bus/Bus_desktop_list";

import GeralScreen from "../ScreenGerais/Main/geralScreen";

import ViajesEspeciales from "../ScreenGerais/dash/viajesEspeciales.js";
import Transfer from "../ScreenGerais/dash/transfer.js";

import ListViajesTransfer from "../ScreenGerais/listaViajes/ListViajesTransfer";
import InfoClientes from "./bus/Bus_desktop_payment";
import InfoClientesTransfer from "../ScreenGerais/infosClientes/infoCliente.Transfer";
import BasicDocument from "../components/pdf";
import PaymentReturn from "../pages/Payment_return";

function RoutesDesktop() {
  return (
    <Routes>
      <Route path="/" element={<Home />} />
      <Route path="/pasajes-bus/:from/:to/:leave_at" element={<BusList />}>
        <Route path=":return_at" element={<BusList />} />
      </Route>
      {/* <Route path="/pasajes-bus/payment" element={<InfoClientesTransfer />} /> */}

      <Route path="/pasajes-transfer" element={<Transfer />} />
      <Route
        path="/pasajes-transfer/:from/:to/:leave_at"
        element={<ListViajesTransfer />}
      />
      <Route
        path="/pasajes-transfer/payment"
        element={<InfoClientesTransfer />}
      />

      {/* <Route path="/busticket/" element={<GeralScreen />} />

      <Route path="/busticket/viajes-especiales" element={<ViajesEspeciales />}/>
    
 */}

      <Route path="/pasajes-bus/payment" element={<InfoClientes />} />

      <Route path="/pdf" element={<BasicDocument />} />

      <Route path="/return-payment/:code/:token" element={<PaymentReturn />} />
    </Routes>
  );
}

export default RoutesDesktop;
