import React from 'react';
import { Box, Button, Header, Card, CardBody, ResponsiveContext, } from 'grommet';
import Stack from '@mui/material/Stack';
import iconBusticket from '../../assets/favicon.ico';
import logo from '../../assets/miniLogo.png';
import api from '../../services/api';
import iconpeople_header from '../../assets/icons/iconpeople-header.png';
import { useNavigate, } from "react-router-dom";
export const HeaderViajes = () => {
    const navigate = useNavigate();

    const size = React.useContext(ResponsiveContext);

    const styles = {
        dropMenuPosMedium: { marginLeft: '80vw', position: 'absolute' },
        dropMenuPosLarge: { marginLeft: '85vw', position: 'absolute' },
        dropMenuPosSmall: { marginRight: '5%' },
    }


    const [menuStatus, setMenuStatus] = React.useState(false);

    return (
        <>
            <Header background="#666666" pad={{
                right: 'large',
                left: 'large',
                top: 'small',
                bottom: 'small'
            }}
                style={{ position: 'absolute', width: '100vw', zIndex: '2' }}>
                <Box direction='row'>
                    <img alt="" src={iconBusticket} style={{ height: '50px' }} />
                    <img alt="" src={logo} style={{ height: '50px' }} />
                </Box>

                <Box>

                </Box>
                <Box direction='row' gap='large'>
                    <Button label="Mis pasajes" color={'transparent'} onClick={() => setMenuStatus(!menuStatus)} />
                    <img alt="" src={iconpeople_header} style={{ height: '50px', color: 'white' }} />
                    {/* <User /> */}
                </Box>
            </Header>
            {
                menuStatus === true ? (<Stack
                    direction="row"
                    justifyContent="flex-end"
                    alignItems="flex-start"
                    style={size === 'large' ? styles.dropMenuPosLarge : size === 'medium' ? styles.dropMenuPosMedium : styles.dropMenuPosSmall}
                >
                    <Card height="100%" width="small" background="light-1">
                        <CardBody pad="medium" gap='small'>
                            <Button width={'large'} label="Ingressar" color={'transparent'} />
                            <Button width={'large'} label="Registrase" color={'transparent'} />
                        </CardBody>
                    </Card>
                </Stack>) : (<></>)
            }
        </>
    )
};





