import React from "react";
import { Main, Box, ResponsiveContext, Grid, Header } from "grommet";

import HeaderMobile from "../components-mobile/header";
import moment from "moment";

import f1 from "../assets/temp/tempwallet.png";
import f2 from "../assets/temp/tempphone.png";
import f3 from "../assets/temp/tempqr.png";
import f4 from "../assets/temp/tempprime.png";

import heli_menu from "../assets/icons/heli-menu.png";
import barco_menu from "../assets/icons/barco-menu.png";
import bus_menu from "../assets/icons/bus-menu.png";
import transfer_menu from "../assets/icons/transfer-menu.png";
import viajeespecial_menu from "../assets/icons/viajeespecial-menu.png";

import { Link } from "react-router-dom";
export const Home = () => {
  return (
    <Main style={{ backgroundColor: "white", height: "100vh" }}>
      <HeaderMobile />

      <Box pad={"medium"}>
        <Box background={"#EBEBEB"} style={{ borderRadius: "5px" }} pad="small">
          <Header
            border={[
              {
                color: "black",
                size: "xsmall",
                style: "solid",
                side: "bottom",
              },
            ]}
          >
            <h2> Mi Saldo disponible</h2>
            <h2> CLP $0</h2>
          </Header>

          <Box
            direction="row"
            align="center"
            justify="center"
            gap="large"
            margin={{ top: "medium" }}
          >
            <Box align="center">
              <img src={f1} style={{ height: "70px", width: "70px" }} alt="" />
              <h2 style={{ textAlign: "center" }}> Recarga Wallet </h2>
            </Box>

            <Box align="center">
              <img src={f2} style={{ height: "70px", width: "70px" }} alt="" />
              <h2 style={{ textAlign: "center" }}> Transferir Saldo </h2>
            </Box>

            <Box align="center">
              <img src={f3} style={{ height: "70px", width: "70px" }} alt="" />
              <h2 style={{ textAlign: "center" }}> Escanear Qr </h2>
            </Box>

            <Box align="center">
              <img src={f4} style={{ height: "70px", width: "70px" }} alt="" />
              <h2 style={{ textAlign: "center" }}> Busticket Prime </h2>
            </Box>
          </Box>
        </Box>

        <h1 style={{ textAlign: "center" }}> Comienza tu viaje! </h1>

        <Box direction="row" justify="center">
          <Link to="/pasajes-bus">
            <img alt="" src={bus_menu} style={{ height: "80px" }} />
          </Link>
          <Link to="/pasajes-transfer">
            <img alt="" src={transfer_menu} style={{ height: "80px" }} />
          </Link>
          <Link to="/busticket/viajes-especiales">
            <img alt="" src={viajeespecial_menu} style={{ height: "80px" }} />
          </Link>
        </Box>
        <Box direction="row" justify="center">
          <Link to="#">
            <img alt="" src={barco_menu} style={{ height: "80px" }} />
          </Link>
          <Link to="#">
            <img alt="" src={heli_menu} style={{ height: "80px" }} />
          </Link>
        </Box>
      </Box>
    </Main>
  );
};

export default Home;
