        buttonTicket: {
            width: '45vh', height: '6vh', backgroundColor: '#F5922F', color: 'white', borderColor: 'transparent', textAlign: 'center', marginBottom: '3vh'
        },
        buttonTicketPagar: {
            width: '20vh', height: '6vh', backgroundColor: '#F5922F', color: 'white', borderColor: 'transparent', textAlign: 'center', marginBottom: '3vh'
        },
        buttonTicketPasajeros: {
            width: '24vh', height: '6vh', backgroundColor: 'white', color: 'gray', borderColor: 'gray', textAlign: 'center', marginBottom: '3vh', whiteSpace: 'nowrap'
        },
        buttonTicketCancelar: {
            width: '12vh', height: '8vh', backgroundColor: 'gray', color: 'white', borderColor: 'transparent', borderRadius: '7%', textAlign: 'center'
        },
